<template>
  <div class="crumb">
    <ul class="nav">
      <li class="item" v-for="(item,index) in crumbInfo">
        <i class="fa fa-home" v-if="index==0"></i>
        <i class="fa fa-angle-right" v-if="index!=0"></i>
        {{item}}
      </li>
    </ul>
  </div>
</template>
<script>
  export default{
    props: {
      crumbInfo: {
        Array
      }
    }
  }
</script>
<style lang="less">
  .crumb{
    .nav{
      height: 82px;
      margin: 0 auto;
      text-align: right;
      line-height: 82px;
      .item{
        display: inline-block;
        font-size: 20px;
        i{
          margin: 0 8px;
          color: #ccc;
        }

        .fa-home{
          font-size: 24px;
          color: #0098ff;
        }
        
      }
    }
  }
</style>


