        scrolled: false,
        newsData: require('../../common/json/new_data.json')
      }
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
      // console.log(this.$refs.news)
      
    },
    computed:{
      
    },
    created(){
      document.documentElement.scrollTop = document.body.scrollTop = 0;

    },
    methods: {
      directoryShow(index) {
        document.documentElement.scrollTop = document.body.scrollTop = 0;
        this.active = index;

      },
      handleScroll () {
        if (this.$refs.news && window.screen.width > 500) {
          this.scrolled = window.scrollY ;
          console.log(window.screen.width )
          if (this.scrolled >= 400) {
            this.$refs.directory.style.position = 'fixed'
          }else{
            this.$refs.directory.style.position = ''
          }
        };
      }
    },
    components: {
      

    }
  }
</script>
