import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index/index'
import goods from '@/components/goods/goods'
import service from '@/components/service/service'
import solution from '@/components/solution/solution'
import GBIM360 from '@/components/GBIM360/GBIM360'
import news from '@/components/news/news'
import about from '@/components/about/about'




Vue.use(Router)

export default new Router({
  routes: [
    {path: '/', redirect: '/index'},
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/goods',
      name: 'goods',
      component: goods
    },
    {
      path: '/service',
      name: 'service',
      component: service
    },
    {
      path: '/GBIM360',
      name: 'GBIM360',
      component: GBIM360
    },
    {
      path: '/solution',
      name: 'solution',
      component: solution
    },
    {
      path: '/news',
      name: 'news',
      component: news
    },
    {
      path: '/about',
      name: 'about',
      component: about
    },

  ],
  linkActiveClass:'active',
})



