<template>
  <div id="app">
    <v-header id="header"></v-header>
    
    <div class="nav-wrapper">
      <div class="nav-title">
        <img class="avatar" src="./logo.png" alt="">
        <i class="fa fa-bars" @click="showNav()"></i>
      </div>
      <transition name="slide-fade">
        <ul class="nav" v-if="show" @click="showNav()" ref="nav">
          <li class="item">
            <img class="avatar" src="./logo.png" alt="">
            <ul class="nav-list" v-if="active.show">
              <li class="logo"></li>
              <li ref="navItem" class="nav-item" v-for="(item, index) in active.data"@click="clickDt(item.id, index)"  :id="item.id" :class="{active: index == isActive}">
                <i>·</i>
                {{item.text}}
              </li>
            </ul>
          </li>
          <li class="item index" @click="active.show=false; active.data=''; isActive = 0">
            <router-link to="/index">首页</router-link>
          </li>
          <li class="line"></li>
          <li class="item goods" @click="active.show=true; active.data=goods; isActive = 0">
            <router-link to="/goods">公司产品</router-link>
          </li>
          <li class="line"></li>
          <li class="item service" @click="active.show=true; active.data=servers; isActive = 0">
            <router-link to="/service">数据服务</router-link>
          </li> 
          <li class="line"></li>
          <li class="item GBIM360">
            <!-- <a href="http://www.gbim360.com/main5.html">gBIM360</a> -->
            <router-link to="/GBIM360">gBIM360</router-link>
          </li> 
          <li class="line"></li>
          <li class="item solution" @click="active.show=true; active.data=solutions; isActive = 0">
            <router-link to="/solution">解决方案</router-link>
          </li> 
          <li class="line"></li>
          <li class="item news" @click="active.show=false; active.data=news; isActive = 0">
            <router-link to="/news">新闻中心</router-link>
          </li> 
          <li class="line"></li>
          <li class="item about" @click="active.show=true; active.data=about; isActive = 0">
            <router-link to="/about">关于我们</router-link>
          </li> 
        </ul>
      </transition>
    </div>
    <router-view id="container" ref="profile"></router-view>
    <v-footer id="footer"></v-footer>
  </div>
</template>

<script>
import header from './components/header/header.vue'
import footer from './components/footer/footer.vue'

import {siblings} from './common/js/date';
export default {
  name: 'app',
  components: {
    'v-header': header,
    'v-footer': footer
  },
  data() {
    return {
      PC: false,
      H5: false,
      show: false,
      seller:{},
      active:{
        show: false,
        data:''
      },
      isActive: 0,
      goods: [
        {
          text:'HOLO3D DATA HUB',
          id: ''
        },
        {
          text:'HOLO3D PRO',
          id: ''
        },
        {
          text:'HOLO3D FOR WEB',
          id: ''
        },
        {
          text:'HOLO3D FOR SERVER',
          id: ''
        },
        {
          text:'GBIM 360',
          id: ''
        }],
      servers: [
        {
          text:'基础底图',
          id: 'baseMap'
        },
        {
          text:'矢量数据',
          id: 'vectorData'
        },
        {
          text:'环境数据',
          id: 'environmentData'
        },
        {
          text:'INSAR',
          id: 'insar'
        }
      ],
      solutions: [
        {
          text:'公路',
          id: '1'
        },
        {
          text:'轨道交通',
          id: ''
        },
        {
          text:'综合管廊',
          id: ''
        },
        {
          text:'数字城市',
          id: ''
        }],
      news: [
        {
          text:'公司动态',
          id: ''
        },
        {
          text:'行业动态',
          id: ''
        },
        {
          text:'媒体报道',
          id: ''
        }],
      about: [
        {
          text:'公司介绍',
          id: 'about'
        },
        {
          text:'联系我们',
          id: 'contact'
        },
        // {
        //   text:'合作',
        //   id: 'compare'
        // }
        ],
      

    }
  },
  created() {
      let width = document.body.clientWidth;
      console.log(width)
      if (width>=500) {
        this.PC = true;
        this.show = true
      }else if(width<500){
        this.H5 = false
        this.show = false
      }
      console.log(this.$refs.navItem)
      
  },
  computed:{
  },
  mounted() {
    // this.$nextTick(function(){
    //   this.getSellerData()
    // })
    
    
  },
  methods:{
    showNav() {
      let width = document.body.clientWidth;
      if (width<=500) {
        this.PC = true;
        this.show = !this.show;
      }
      
    },
    clickDt(id, index){ 
      // event.srcElement.style.color='#fe4817'
      // for(let i = 0; i < siblings(event.srcElement).length ; i++){
      //   let dom = siblings(event.srcElement)[i]
      //   let id = dom.getAttribute('id')
      //   if (id) dom.style.color=''
      // }
      this.$refs.profile.show(id);
      this.isActive = index
       
      console.log(id)
    }
   

  }
  
}
</script>

