  }
  .slide-fade-leave-active {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to{
    transform: translateY(-350px);
    height: 0;
  }
  




  #header{
    display: none;
  }
  

  .nav-wrapper{
    position: fixed;
    top: 0;
    z-index: 100;
    width: 100%;
    background-color: #333;
    .nav-title{
      position: relative;
      z-index: 100;
      width: 90%;
      height: 49px;
      padding-left: 10%;
      border-bottom: 1px solid #666;
      background-color: #333;
      line-height: 70px;
      img{
        // margin: 9px 0;
      }
      .fa-bars{
        position: absolute;
        right: 10px;
        display: inline-block;
        color: rgba(255, 255, 255, 0.5);
        font-size: 30px;
        padding: 0px 10px;
        line-height: 50px;
        
      }
    }
    .nav{
      position: fixed;
      top: 48px;
      z-index: 10;
      line-height: 50px;
      height: 350px;
      background-color: #333;
      width: 100%;
      .item{
        width: 100%;
        padding-left: 10%;
        &:first-child{
          display: none;
        }
        .nav-list{
          display: none;
        }
        & > a{
          display: block;
          font-size: 15px;
          color: #fff;
        }
        & .active{
          color: #09f;
        }
        .avatar{
          width: 126px;
          height: 28px;
          vertical-align: middle;
        }
        
      }
      .line{
        display: none;
      }

    }
  }

  #container{
    // display: none;
  }

}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // text-align: center;
  color: #2c3e50;
}
@media screen and(min-device-width:500px){
  #header{
    display: block;
  }
  .nav-wrapper{
    position: relative;
    z-index: 10;
    width: 100%;
    background-color: rgba(9, 8, 24, 0.5);
    .nav-title{
      display: none;
    }
    .nav{
      display: flex;
      width: 70%;
      height: 56px;
      line-height: 56px;
      margin-left: 10%;
      .item{
        flex:1;
        text-align: center;
        &:first-child{
