        }
        .nav-list{
          position: relative;
          top: -100px;
          background-color: #fff;
          z-index: -1;
          
          .logo{
            width: 240px;
            height: 140px;
            background-color: #0099ff; 
          }
          .nav-item{
            width: 220px;
            height: 48px;
            font-size: 15px;
            text-align: left;
            border-bottom: 1px solid #f7f7f7;
            padding-left: 20px;
            cursor: pointer;
            &.active{
              color: #09f;
            }
            i{
              margin-right: 10px;
              color: #0098ff;
              font-weight: 900;
            }
          }
        }
        & > a{
          display: block;
          font-size: 15px;
          color: #fff;
        }
        & .active{
          color: #09f
        }
        .avatar{
          width: 126px;
          height: 28px;
          vertical-align: middle;
        }
        
      }
      .line{
        position: relative;
        top: 22px;
        border-left: 1px solid rgba(255, 255, 255, 0.2);
        height: 12px;
      }

    }
  }
  #container{
    position: relative;
    top: -56px;
  }
}





</style>



